class OneLineChart extends React.Component {
    constructor(props) {
        super(props);
        this.id = '_' + uuidv().replace(/-/g, '')
        this.getId = this.getId.bind(this)
        this.setId = this.setId.bind(this)


    }
    getId(id) {
        return '#' + this.id + '-' + id;
    }

    setId(id) {
        return this.id + '-' + id;
    }

    componentDidMount() {
        // let width = d3.select(this.getId("svg-container")).node().getBoundingClientRect().width
        let width = 330
        let endRectHeight = 8
        let middleRectHeight = 40
        let widthRectData = 50
        let heightRectData = 30
        let lineY = 100
        let svg = d3.select(this.getId("svg"))
            .attr("width",width)
            .attr("height",200)
        let linearGradient = svg.append("linearGradient")
            .attr("id",this.setId("BarGradient"))

        linearGradient.append("stop")
            .attr("offset","0%")
            .attr("stop-color","#51b777")
        linearGradient.append("stop")
            .attr("offset","50%")
            .attr("stop-color","#ffd238")


        linearGradient.append("stop")
            .attr("offset","100%")
            .attr("stop-color","#c3381f")

        //start rect
        svg.append("rect")
            .attr("fill","#ffffff")
            .attr("x",0)
            .attr("y",function () {
                return lineY - endRectHeight / 2 + 1
            })
            .attr("width", 2)
            .attr("height", endRectHeight)

        svg.append("text")
            .attr("fill" , "#FFFFFF")
            .attr("x" , -2)
            .attr("y",function () {
                return lineY + endRectHeight / 2
            })
            .attr("transform",function () {
                let margin_right = -3
                return "translate(" +margin_right+",0)"
            })
            .append("tspan")
            .text("start")
        //end rect
        svg.append("rect")
            .attr("fill","#ffffff")
            .attr("x",width)
            .attr("y",function () {
                return lineY - endRectHeight / 2 + 1
            })
            .attr("width", 2)
            .attr("height", endRectHeight)

        svg.append("text")
            .attr("fill" , "#FFFFFF")
            .attr("text-anchor" , "end")
            .attr("x" , width)
            .attr("y",function () {
                return lineY + endRectHeight / 2
            })
            .attr("transform",function () {
                let margin_right = 5
                return "translate(" +margin_right+",0)"
            })
            .append("tspan")
            .text("end")
        //rect middle
        svg.append("rect")
            .attr("fill","#ffffff")
            .attr("x",width / 2)
            .attr("y",function () {
                return lineY + 5
            })
            .attr("width", 2)
            .attr("height", middleRectHeight)

        svg.append("text")
            .attr("fill" , "#FFFFFF")
            .attr("text-anchor" , "middle")
            .attr("x" , width/2)
            .attr("y",function () {
                return lineY + middleRectHeight + 20
            })
            .attr("transform",function () {
                let margin_right = 0
                return "translate(" +margin_right+",0)"
            })
            .append("tspan")
            .text("middle")



        svg.append("rect")
            .attr("x",0)
            .attr("y",lineY)
            .attr("height",2)
            .attr("width",width)
            .style("fill","url("+this.getId("BarGradient")+")")
            .style("stroke-width",2)

        //data 1

        let data1_g = svg.append("g")
            .attr("transform",function () {
                let translateX = width * 10 / 100
                let margin_bottom = 10
                let translateY = lineY - margin_bottom
                return "translate("+translateX+","+translateY+")"
            })
        data1_g
            .append("rect")
            .attr("fill","#51b777")
            .attr("rx",5)
            .attr("x",0)
            .attr("y",function () {
                return -1 * heightRectData
            })
            .attr("width",function () {
                return widthRectData
            })
            .attr("height",heightRectData)


        data1_g
            .append("text")
            .attr("text-anchor","middle")
            .attr("fill","white")
            .attr("x",function () {
                return widthRectData / 2
            })
            .attr("y",function () {
                return -1 * heightRectData / 2 + 5
            })
            .append("tspan")
            .text("data 1")

        // data 2
        let data2_g = svg.append("g")
            .attr("transform",function () {
                let translateX = width * 80 / 100
                let margin_bottom = 10
                let translateY = lineY - margin_bottom
                return "translate("+translateX+","+translateY+")"
            })
        data2_g
            .append("rect")
            .attr("rx",5)
            .attr("fill","#c37825")
            .attr("x",0)
            .attr("y",function () {
                return -1 * heightRectData
            })
            .attr("width",function () {
                return widthRectData
            })
            .attr("height",heightRectData)


        data2_g
            .append("text")
            .attr("text-anchor","middle")
            .attr("fill","white")
            .attr("x",function () {
                return widthRectData / 2
            })
            .attr("y",function () {
                return -1 * heightRectData / 2 + 5
            })
            .append("tspan")
            .text("data 2")
    };

    render() {
        return (
            <div id={this.setId("svg-container")} align={"center"}>
                <svg id={this.setId("svg")} className={"svg"}>

                </svg>
            </div>
        );
    }
}

export default OneLineChart;
