/*
    this component must change with correct formula
*/
import React from "react";
import uuidv from "uuid/v4";
import * as d3 from "d3";
import {Tooltip} from "react-bootstrap";
class RectangleD3 extends React.Component {
    constructor() {
        super()
        this.id = '_' + uuidv().replace(/-/g, '')
        this.getId = this.getId.bind(this)
        this.setId = this.setId.bind(this)
    }

    getId(id) {
        return '#' + this.id + '-' + id;
    }

    setId(id) {
        return this.id + '-' + id;
    }
    build() {
        let rectMouseOver = (d, i, nodes ) => {
            let node = d3.select(nodes[i])
            node
                .attr("opacity",0.8)


        }

        let rectMouseLeave= (d, i, nodes) => {

            console.log("mouse leave")
            d3.select(nodes[i])
                .attr("opacity", 1)

            d3.select(this.getId("mytooltip"))
                .style("display", "none")

        }

        let width = 360;
        let height = 300;
        let svg = d3.select(this.getId("svg"))
            .attr("width", width)
            .attr("height", height)

        svg.append("rect")
            .attr("x",0)
            .attr("y",0)
            .attr("width",width/2)
            .attr("height",height)
            .attr("fill","rgb(43, 215, 132)")
            .on("mouseover",function (d,i,nodes) {

                rectMouseOver(d,i,nodes)
            })
            .on("mouseleave",function (d,i,nodes) {
                rectMouseLeave(d,i,nodes)
            })
        svg.append("g")
            .attr("transform",function () {
                let tranlateX = (width/2)/2
                let tranlateY = height/2

                return "translate("+tranlateX+","+tranlateY+")"
            })
            .append("text")
            .attr("text-anchor", "middle")
            .append("tspan")
            .text("example 1")




        svg.append("rect")
            .attr("x",width/2 + 1)
            .attr("y",0)
            .attr("width",function () {
                return 5*width/4 - width
            })
            .attr("height",function () {
                return height * 3 / 4
            })
            .attr("fill","rgb(43, 215, 132)")
            .on("mouseover",function (d,i,nodes) {

                rectMouseOver(d,i,nodes)
            })
            .on("mouseleave",function (d,i,nodes) {
                rectMouseLeave(d,i,nodes)
            })
        svg.append("g")
            .attr("transform",function () {
                let translateX = ((width/2+1) + (width/2+1) + (5*width/4-width))/2
                let translateY = height*3/4/2
                return "translate("+ translateX +","+ translateY +")"
            })
            .append("text")
            .attr("text-anchor", "middle")
            .append("tspan")
            .text("example 2")


        svg.append("rect")
            .attr("x",function () {
                return 3*width/4 + 2
            })
            .attr("y",0)
            .attr("width",function () {
                return width/4
            })

            .attr("height",function () {
                return height * 3 / 4
            })
            .attr("fill","rgb(43, 215, 132)")
            .on("mouseover",function (d,i,nodes) {

                rectMouseOver(d,i,nodes)
            })
            .on("mouseleave",function (d,i,nodes) {
                rectMouseLeave(d,i,nodes)
            })
        svg.append("g")
            .attr("transform",function () {

                let translateX = ((3*width/4 + 2) + (3*width/4 + 2) + (width/4))/2
                let translateY = height*3/4/2
                return "translate("+ translateX +","+ translateY+")"
            })
            .append("text")
            .attr("text-anchor", "middle")
            .append("tspan")
            .text("example 3")

        svg.append("rect")
            .attr("x",function () {
                return width/2 + 1
            })
            .attr("y",function () {
                return height * 3 / 4 + 1
            })
            .attr("width",function () {
                return width/2
            })

            .attr("height",function () {
                return height / 4
            })
            .attr("fill","rgb(230,65,65)")
            .on("mouseover",function (d,i,nodes) {
                rectMouseOver(d,i,nodes)
            })
            .on("mouseleave",function (d,i,nodes) {
                rectMouseLeave(d,i,nodes)
            })
        svg.append("g")
            .attr("transform",function () {
                let translateX = ((width/2 + 1) + (width/2 + 1) + (width/2))/2
                let translateY = ((height * 3 / 4 + 1) + ((height * 3 / 4 + 1)+height/4 + 1)) / 2

                return "translate("+ translateX +","+ translateY +")"
            })
            .append("text")
            .attr("text-anchor", "middle")
            .append("tspan")
            .text("example 4")



    }

    componentDidMount() {
        this.build()
    }

    render() {
        return (
            <div id={this.setId("svg-container")} align={"center"}>
                <Tooltip id={this.setId("mytooltip")} style={{opacity:0}} >
                    <div style={{color:"white"}} id={this.setId("tooltip-text")}>
                    </div>
                </Tooltip>
                <svg id={this.setId("svg")} className={"svg"}>

                </svg>

            </div>
        );
    }
}

export default RectangleD3
