import React from "react";
import * as d3 from "d3";
import "../scss/bar-chart-d3-horizontal.scss"
import uuidv from "uuid/v4";
class BarChartD3Horizontal extends React.Component{
    constructor(props){
        super(props)
        this.id = '_' + uuidv().replace(/-/g, '')
        this.getId = this.getId.bind(this)
        this.setId = this.setId.bind(this)


    }
    getId(id) {
        return '#' + this.id + '-' + id;
    }

    setId(id) {
        return this.id + '-' + id;
    }

    build() {
        let width = d3.select(this.getId("svg-container")).node().getBoundingClientRect().width;
        let data = [
            {value: 8,name:"نمونه اول"},
            {value: 13,name:"نمونه دوم"}
        ];
        let height = 216;
        let bar_height = 72;
        let margin = {
            bottom:40,
            top:40,
            right:20,
            left:20
        }
        let scale_factor = width/21;
        let margin_bar = 24
        let svg = d3.select(this.getId("svg"))
            .attr("width", width)
            .attr("height", height + margin.bottom)
            .attr("transform","translate(0,"+margin.top+")")
        svg.empty()
        svg.selectAll("*").remove();
        let linearGradient = svg.append("linearGradient")
            .attr("id",this.setId("BarGradient"))

        linearGradient.append("stop")
            .attr("offset","0%")
            .attr("stop-color","rgb(21, 27, 36)")
            .attr("stop-opacity","0.5")

        linearGradient.append("stop")
            .attr("offset","100%")
            .attr("stop-color","rgb(21, 27, 36)")
            .attr("stop-opacity","0.8")


        svg.append("rect")
            .attr("width", width)
            .attr("height", height)
            .attr("fill", "#E64141")

        svg.append("text")
            .text("قرمز")
            .attr("fill","#E64141")
            .attr("transform", function (d, i) {
                let traslateY = height + 10
                return "translate(" + width + ", "+ traslateY + ") rotate(315)"
            })
        svg.append("rect")
            .attr("width", width * 0.64)
            .attr("height", height)
            .attr("fill", "#FFC701")

        svg.append("text")
            .text("زرد")
            .attr("fill","#FFC701")
            .attr("transform", function (d, i) {
                let traslateY = height + 10
                return "translate(" + width*0.64 + ", "+ traslateY + ") rotate(315)"
            })

        svg.append("rect")
            .attr("width", width * 0.45)
            .attr("height", height)
            .attr("fill", "#2BD784")
        svg.append("text")
            .text("سبز")
            .attr("fill","#2BD784")
            .attr("transform", function (d, i) {
                let traslateY = height + 10
                return "translate(" + width*0.45+ ", "+ traslateY + ") rotate(315)"
            })

        let bar = svg.selectAll("g")
            .data(data)
            .enter()
            .append("g")
            .attr("transform", function (d, i) {
                let translate = (i * bar_height ) + margin_bar*(i+1)
                return "translate(0," + translate + ")"
            })


        bar.append("rect")
            .attr("width", function (d) {
                return d.value * scale_factor;
            })
            .attr("height", bar_height - 1)
            .attr("fill", "url("+this.getId("BarGradient")+")")

        //end-line-bar
        bar.append("line")
            .attr("x1",function (d,i) {
                return d.value * scale_factor;
            })
            .attr("y1",function (d,i) {
                console.log("i",i)
                return  0
            })
            .attr("x2",function (d,i) {
                return d.value * scale_factor;
            })

            .attr("y2",function (d,i) {
                return bar_height
            })
            .attr("class","endline-bar")

        bar.append("text")
            .text(function (d,i) {
                return d.name;
            })
            .attr("class","label-name")
            .attr("transform", function (d, i) {

                let translateX = d.value*scale_factor - 10
                return "translate("+ translateX +"," + bar_height/3 + ")"
            })
        bar.append("text")
            .text(function (d,i) {
                return "$" + d.value;
            })
            .attr("class","label-value")

            .attr("transform", function (d, i) {
                let translateX = d.value*scale_factor - 10
                return "translate("+ translateX +"," + bar_height*2/3 + ")"
            })

        //Difference line
        svg.append("rect")
            .attr("width",function () {
                return Math.abs(data[0].value - data[1].value) * scale_factor
            })
            .attr("fill","#2BD784")
            .attr("height",1)
            .attr("transform", function () {
                let translateX = Math.min(data[0].value,data[1].value) * scale_factor
                let translateY = -10
                return "translate("+ translateX +"," + translateY + ")"
            })
        svg.append("line")
            .attr("x1",function () {
                return Math.min(data[0].value,data[1].value) * scale_factor
            })
            .attr("y1",-10) // margin top
            .attr("x2",function () {
                return Math.min(data[0].value,data[1].value) * scale_factor
            })
            .attr("y2",margin_bar)
            .attr("class","difference-line-to-bar")
        svg.append("line")
            .attr("x1",function () {
                return Math.max(data[0].value,data[1].value) * scale_factor
            })
            .attr("y1",-10) // margin top
            .attr("x2",function () {
                return Math.max(data[0].value,data[1].value) * scale_factor
            })
            .attr("y2",margin_bar*2+bar_height)
            .attr("class","difference-line-to-bar")

        svg.append("text")
            .text(function () {
                return "$" + Math.abs(data[0].value-data[1].value)
            })
            .attr("transform", function () {
                let translateX = Math.min(data[0].value,data[1].value) * scale_factor
                translateX = translateX + 15 //margin left
                let translateY = -15
                return "translate("+ translateX +"," + translateY + ")"
            })
            .attr("class","difference-text")
    }

    componentDidMount() {
        this.build()
        d3.select(window).on("resize",() => {
            this.build()
        })
    }
    render() {
        return(
            <div className={"row svg-container"} id={this.setId("svg-container")} >

                <svg id={this.setId("svg")} className={'svg'}>

                </svg>
            </div>
        )
    }
}
export default BarChartD3Horizontal