import React from "react";
import GaugeChart from 'react-gauge-chart'
import * as d3 from 'd3'

class GaugeDiagram extends React.Component{
    componentDidMount() {
        d3.select("#gauge-chart5")
            .attr('width',100)
    }

    render() {
        return(
            <div align={"center"}>
                <GaugeChart id="gauge-chart5"
                  style={{width:'100%'}}
                  animate={true}
                  arcsLength={[0.3, 0.5, 0.2]}
                  colors={['rgb(43, 215, 132)', '#f5f018', 'rgb(230,65,65)',]}
                  arcPadding={0.01}
                />
            </div>

        )
    }
}
export default GaugeDiagram
